import * as Yup from 'yup';
import { useSnackbar } from 'notistack';
import { useCallback, useEffect, useState } from 'react';
import { Form, FormikProvider, useFormik } from 'formik';
import { Icon } from '@iconify/react';
import eyeFill from '@iconify/icons-eva/eye-fill';
import closeFill from '@iconify/icons-eva/close-fill';
import eyeOffFill from '@iconify/icons-eva/eye-off-fill';
// material
import {
  Box,
  Grid,
  Card,
  Stack,
  Switch,
  TextField,
  FormControlLabel,
  Typography,
  FormHelperText,
  IconButton,
  InputAdornment
} from '@mui/material';
import { LoadingButton } from '@mui/lab';
// hooks
import useAuth from '../../../hooks/useAuth';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
//
import { useDispatch, useSelector } from '../../../redux/store';
import { getCountries, getCity } from '../../../redux/slices/user';
import Request from '../../../utils/Request';
import { handlePermission } from '../../../utils/handlePermission';
import { PERMISSIONS } from '../../../guards/Permissions';
import { UploadAvatar } from '../../upload';
import { fData } from '../../../utils/formatNumber';
// ----------------------------------------------------------------------

export default function AccountGeneral() {
  const isMountedRef = useIsMountedRef();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const { user, initialize } = useAuth();
  const { countries, city } = useSelector((state) => state.user);
  const [showPassword, setShowPassword] = useState(false);
  const [open, setOpen] = useState(false);

  const UpdateUserSchema = Yup.object().shape({
    companyname: Yup.string().required('Company Name is required'),
    username: Yup.string().required('Pseudoname is required'),
    first_name: Yup.string().required('Name is required'),
    last_name: Yup.string().required('Name is required'),
    phone: Yup.number().required('Phone is required'),
    delete_fiche_min: Yup.number().required('minute to delete fiche is required'),
    country_id: Yup.string().required('Country is required'),
    city_id: Yup.string().required('City is required'),
    addresse_complet: Yup.string().required('Address is required'),
    description: Yup.string().required('Description is required'),
    password: Yup.string().nullable().min(6, 'Password should be of minimum 6 characters length'),
    patente: Yup.boolean().required('Patente is required'),
    mariage_min: Yup.number().required('Mariage min is required'),
    mariage_nb: Yup.number().required('Mariage Nb is required')
  });

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      companyname: user?.companyname || null,
      username: user?.username || null,
      first_name: user?.first_name || null,
      last_name: user?.last_name || null,
      delete_fiche_min: user?.delete_fiche_min || null,
      phone: user?.phone || null,
      password: null,
      country_id: user?.country || null,
      addresse_complet: user?.addresse_complet || null,
      city_id: user?.city || null,
      description: user?.description || null,
      mariage_min: user?.mariage_min || null,
      mariage_nb: user?.mariage_nb || null,
      mariage_gratuit: user?.mariage_gratuit,
      mariage_option: user?.mariage_option,
      patente: user?.patente,
      qrcode: user?.qrcode,
      whatsapp: user?.whatsapp,
      boulereverce: user?.boulereverce,
      companyphoneIn: user?.companyphoneIn,
      agentnameIn: user?.agentnameIn,
      agentaddressIn: user?.agentaddressIn,
      multitirageIn: user?.multitirageIn,
      mariageautoIn: user?.mariageautoIn,
      l3autoIn: user?.l3autoIn,
      l4autoIn: user?.l4autoIn,
      boulepaireIn: user?.boulepaireIn,
      pointeIn: user?.pointeIn,
      user_photo: user?.user_photo
    },

    validationSchema: UpdateUserSchema,
    onSubmit: async (values, { setErrors, resetForm, setSubmitting }) => {
      try {
        const formData = new FormData();
        formData.append('companyname', values.companyname);
        formData.append('username', values.username);
        if (open === true) {
          formData.append('user_photo', values.user_photo);
        }
        formData.append('first_name', values.first_name);
        formData.append('last_name', values.last_name);
        formData.append('delete_fiche_min', values.delete_fiche_min);
        formData.append('phone', values.phone);
        formData.append('password', values.password);
        formData.append('country_id', values.country_id);
        formData.append('addresse_complet', values.addresse_complet);
        formData.append('city_id', values.city_id);
        formData.append('description', values.description);
        formData.append('mariage_min', values.mariage_min);
        formData.append('mariage_nb', values.mariage_nb);
        formData.append('mariage_gratuit', values.mariage_gratuit ? 'True' : 'False');
        formData.append('mariage_option', values.mariage_option ? 'True' : 'False');
        formData.append('patente', values.patente ? 'True' : 'False');
        formData.append('qrcode', values.qrcode ? 'True' : 'False');
        formData.append('whatsapp', values.whatsapp ? 'True' : 'False');
        formData.append('boulereverce', values.boulereverce ? 'True' : 'False');
        formData.append('companyphoneIn', values.companyphoneIn ? 'True' : 'False');
        formData.append('agentnameIn', values.agentnameIn ? 'True' : 'False');
        formData.append('agentaddressIn', values.agentaddressIn ? 'True' : 'False');
        formData.append('multitirageIn', values.multitirageIn ? 'True' : 'False');
        formData.append('mariageautoIn', values.mariageautoIn ? 'True' : 'False');
        formData.append('l3autoIn', values.l3autoIn ? 'True' : 'False');
        formData.append('l4autoIn', values.l4autoIn ? 'True' : 'False');
        formData.append('boulepaireIn', values.boulepaireIn ? 'True' : 'False');
        formData.append('pointeIn', values.pointeIn ? 'True' : 'False');
        await Request.handleModifyProfile(formData);
        await initialize();
        setSubmitting(false);
        enqueueSnackbar('Mettre à jour avec succès', { variant: 'success' });
      } catch (error) {
        if (error?.message) {
          enqueueSnackbar(error?.message, { variant: 'error' });
        } else {
          alert(JSON.stringify(error));
        }
        setSubmitting(false);
        setErrors(error);
      }
    }
  });

  useEffect(() => {
    dispatch(getCountries());
    dispatch(getCity());
  }, [dispatch]);

  const { values, errors, touched, isSubmitting, handleSubmit, getFieldProps, setFieldValue } = formik;

  const handleDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];

      if (file) {
        setFieldValue(
          'user_photo',
          Object.assign(file, {
            preview: URL.createObjectURL(file)
          })
        );
      }
      setOpen(true);
    },
    [setFieldValue]
  );

  return (
    <FormikProvider value={formik}>
      <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={9}>
            <Card sx={{ p: 3 }}>
              <Stack spacing={3}>
                {handlePermission(user?.perms, PERMISSIONS.CHANGE_LOGO) && (
                  <Stack>
                    <Box sx={{ mb: 5 }}>
                      <UploadAvatar
                        accept="image/*"
                        file={values.user_photo}
                        maxSize={3145728}
                        onDrop={handleDrop}
                        error={Boolean(touched.user_photo && errors.user_photo)}
                        caption={
                          <Typography
                            variant="caption"
                            sx={{
                              mt: 2,
                              mx: 'auto',
                              display: 'block',
                              textAlign: 'center',
                              color: 'text.secondary'
                            }}
                          >
                            Allowed *.jpeg, *.jpg, *.png
                            <br /> max size of {fData(3145728)}
                          </Typography>
                        }
                      />
                      <FormHelperText error sx={{ px: 2, textAlign: 'center' }}>
                        {touched.user_photo && errors.user_photo}
                      </FormHelperText>
                    </Box>
                  </Stack>
                )}
                <Stack direction={{ xs: 'column', md: 'row' }} spacing={2}>
                  <TextField
                    fullWidth
                    disabled
                    label="Nom de l'entreprise"
                    placeholder="Entrer le nom de l'entreprise"
                    {...getFieldProps('companyname')}
                    error={Boolean(touched.companyname && errors.companyname)}
                    // helperText={touched.companyname && errors.companyname}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, PERMISSIONS.CHANGE_USER_INFO)}
                    fullWidth
                    label="Pseudoname"
                    placeholder="Entrer le pseudoname"
                    {...getFieldProps('username')}
                    error={Boolean(touched.username && errors.username)}
                    // helperText={touched.username && errors.username}
                  />
                </Stack>
                <Stack direction={{ xs: 'column', md: 'row' }} spacing={2}>
                  <TextField
                    disabled={!handlePermission(user?.perms, PERMISSIONS.CHANGE_USER_INFO)}
                    fullWidth
                    label="Prénom"
                    placeholder="Entrer le prénom"
                    {...getFieldProps('first_name')}
                    error={Boolean(touched.first_name && errors.first_name)}
                    // helperText={touched.first_name && errors.first_name}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, PERMISSIONS.CHANGE_USER_INFO)}
                    fullWidth
                    label="Nom de famille"
                    placeholder="Entrer le nom de famille"
                    {...getFieldProps('last_name')}
                    error={Boolean(touched.last_name && errors.last_name)}
                    // helperText={touched.last_name && errors.last_name}
                  />
                </Stack>

                <Stack direction={{ xs: 'column', md: 'row' }} spacing={2}>
                  <TextField
                    disabled={!handlePermission(user?.perms, PERMISSIONS.CHANGE_USER_INFO)}
                    fullWidth
                    label="Numéro de téléphone"
                    {...getFieldProps('phone')}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, PERMISSIONS.CHANGE_USER_INFO)}
                    select
                    fullWidth
                    label="Pays"
                    placeholder="Choisir le pays"
                    {...getFieldProps('country_id')}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.country_id && errors.country_id)}
                    // helperText={touched.country_id && errors.country_id}
                  >
                    <option value="" />
                    {countries?.map((option) => (
                      <option key={option.id} value={option.id}>
                        {option.name}
                      </option>
                    ))}
                  </TextField>
                </Stack>

                <Stack direction={{ xs: 'column', md: 'row' }} spacing={2}>
                  <TextField
                    disabled={!handlePermission(user?.perms, PERMISSIONS.CHANGE_USER_INFO)}
                    fullWidth
                    label="Ville"
                    {...getFieldProps('city_id')}
                    error={Boolean(touched.city_id && errors.city_id)}
                    // helperText={touched.city_id && errors.city_id}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, PERMISSIONS.CHANGE_USER_INFO)}
                    fullWidth
                    label="Addresse complete"
                    {...getFieldProps('addresse_complet')}
                    error={Boolean(touched.addresse_complet && errors.addresse_complet)}
                    // helperText={touched.addresse_complet && errors.addresse_complet}
                  />
                </Stack>

                {handlePermission(user?.perms, PERMISSIONS.CHANGE_PASSWORD) && (
                  <Stack>
                    <TextField
                      fullWidth
                      autoComplete="current-password"
                      type={showPassword ? 'text' : 'password'}
                      label="Mot de passe"
                      {...getFieldProps('password')}
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <IconButton edge="end" onClick={() => setShowPassword((prev) => !prev)}>
                              <Icon icon={showPassword ? eyeFill : eyeOffFill} />
                            </IconButton>
                          </InputAdornment>
                        )
                      }}
                      error={Boolean(touched.password && errors.password)}
                      // helperText={touched.password && errors.password}
                    />
                  </Stack>
                )}

                <TextField
                  disabled={!handlePermission(user?.perms, PERMISSIONS.CHANGE_USER_INFO)}
                  {...getFieldProps('description')}
                  fullWidth
                  multiline
                  minRows={4}
                  maxRows={4}
                  label="Description"
                  error={Boolean(touched.description && errors.description)}
                  // helperText={touched.description && errors.description}
                />
                <Stack>
                  <TextField
                    disabled={!handlePermission(user?.perms, PERMISSIONS.CHANGE_USER_INFO)}
                    fullWidth
                    label="Minutes pour supprimer un fiche"
                    {...getFieldProps('delete_fiche_min')}
                    error={Boolean(touched.delete_fiche_min && errors.delete_fiche_min)}
                    // helperText={touched.delete_fiche_min && errors.delete_fiche_min}
                  />
                </Stack>
                {handlePermission(user?.perms, PERMISSIONS.MARIAGE_FREE) && (
                  <Stack direction={{ xs: 'column', md: 'row' }} spacing={2}>
                    <TextField
                      disabled={!values?.mariage_gratuit}
                      fullWidth
                      label="Mariage valeur"
                      {...getFieldProps('mariage_min')}
                      error={Boolean(touched.mariage_min && errors.mariage_min)}
                      // helperText={touched.mariage_min && errors.mariage_min}
                    />
                    <TextField
                      disabled={!values?.mariage_gratuit}
                      fullWidth
                      label="Mariage quantite"
                      {...getFieldProps('mariage_nb')}
                      error={Boolean(touched.mariage_nb && errors.mariage_nb)}
                      // helperText={touched.mariage_nb && errors.mariage_nb}
                    />
                    <FormControlLabel
                      control={
                        <Switch
                          {...getFieldProps('mariage_gratuit')}
                          checked={values?.mariage_gratuit}
                          color={!values?.mariage_gratuit ? 'primary' : 'success'}
                        />
                      }
                      labelPlacement="start"
                      label="Mariage Gratuit"
                    />
                  </Stack>
                )}
                {handlePermission(user?.perms, PERMISSIONS.OPTION_PLUS) && (
                  <>
                    <Stack direction={{ xs: 'column', md: 'row' }} spacing={2}>
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('patente')}
                            checked={values?.patente}
                            color={!values?.patente ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Patente"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('mariage_option')}
                            checked={values?.mariage_option}
                            color={!values?.mariage_option ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Mariage Option"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('qrcode')}
                            checked={values?.qrcode}
                            color={!values?.qrcode ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="QrCode"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('whatsapp')}
                            checked={values?.whatsapp}
                            color={!values?.whatsapp ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Whatsapp"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('boulereverce')}
                            checked={values?.boulereverce}
                            color={!values?.boulereverce ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Boule a revers"
                      />
                    </Stack>
                    <Stack direction={{ xs: 'column', md: 'row' }} spacing={2}>
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('companyphoneIn')}
                            checked={values?.companyphoneIn}
                            color={!values?.companyphoneIn ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Company Phone"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('agentnameIn')}
                            checked={values?.agentnameIn}
                            color={!values?.agentnameIn ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Vendeur name"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('agentaddressIn')}
                            checked={values?.agentaddressIn}
                            color={!values?.agentaddressIn ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Vendeur addresse"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('multitirageIn')}
                            checked={values?.multitirageIn}
                            color={!values?.multitirageIn ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Multi tirage"
                      />
                    </Stack>
                    <Stack direction={{ xs: 'column', md: 'row' }} spacing={2}>
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('boulepaireIn')}
                            checked={values?.boulepaireIn}
                            color={!values?.boulepaireIn ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Boule Paire"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('pointeIn')}
                            checked={values?.pointeIn}
                            color={!values?.pointeIn ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Pointe auto"
                      />

                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('mariageautoIn')}
                            checked={values?.mariageautoIn}
                            color={!values?.mariageautoIn ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Mariage auto"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('l3autoIn')}
                            checked={values?.l3autoIn}
                            color={!values?.l3autoIn ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Lotto 3 auto"
                      />
                      <FormControlLabel
                        control={
                          <Switch
                            {...getFieldProps('l4autoIn')}
                            checked={values?.l4autoIn}
                            color={!values?.l4autoIn ? 'primary' : 'success'}
                          />
                        }
                        labelPlacement="start"
                        label="Lotto 4 auto"
                      />
                    </Stack>
                  </>
                )}
              </Stack>

              <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                  Sauvegarder les modifications
                </LoadingButton>
              </Box>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
